import axios from "axios";
import React, { useEffect } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useRouteMatch,
  Link,
} from "react-router-dom";
import ReactMarkdown from "react-markdown";
import rehypeRaw from "rehype-raw";
import remarkRemoveComments from "remark-remove-comments";
import remarkGfm from "remark-gfm";
// import remarkCodeExtra from "remark-code-extra";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { oneLight } from "react-syntax-highlighter/dist/cjs/styles/prism";
import styled from "@emotion/styled";

const token = "Z2hwX0F2d3dLME4zN0hNbDYzWVBoTWVNMmZyVXBoR3hSZjBBMW1Ldg==";

const TITLE_MAP = {
  "ts-faq/1.md": `[] TypeScript FAQ - Common "Bugs" That Aren't Bugs`,
} as Record<string, string>;

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/about">
          <div>about</div>
        </Route>
        <Route path="/users">
          <div>users</div>
        </Route>
        <Route path="/p/*" component={Post}></Route>
        <Route path="/" component={Main}></Route>
      </Switch>
    </Router>
  );
}

function Post() {
  const match = useRouteMatch("/p/*") as any;
  const [rawMd, setRawMd] = React.useState("");
  const [isNotFound, setIsNotFound] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(true);

  useEffect(() => {
    const url = `https://raw.githubusercontent.com/alxtz/obs-vault/main/${
      match.params[0]
    }&timestamp=${new Date().getTime()}`;

    console.log("url", url);

    const fetchData = async () => {
      try {
        const response = await axios.get(url, {
          headers: {
            Authorization: `token ${atob(token)}`,
          },
        });
        const data = response.data;
        setRawMd(data);
        setIsNotFound(false);
        setIsLoading(false);
      } catch (error) {
        console.log("e", error);
        setIsNotFound(true);
        setIsLoading(false);
      }
    };

    fetchData();
  }, [match.params]);

  // return <p>{JSON.stringify(match?.params)}</p>;
  if (isLoading) {
    return <p>Loading...</p>;
  }

  if (isNotFound) {
    return <p>Post not found</p>;
  }

  return (
    <div className="markdown-body">
      <PageContainer>
        <BlogContainer>
          {TITLE_MAP[match.params[0]] ? (
            <BlogPostTitle>{TITLE_MAP[match.params[0]]}</BlogPostTitle>
          ) : null}
          <ReactMarkdown
            children={rawMd}
            // remarkPlugins={[remarkRemoveComments, remarkGfm, remarkCodeExtra]}
            rehypePlugins={[rehypeRaw]}
            remarkPlugins={[remarkRemoveComments, remarkGfm]}
            components={CodeBlock}
          />
        </BlogContainer>
      </PageContainer>
    </div>
  );
}

const PageContainer = styled.div``;

const BlogContainer = styled.div`
  min-width: 70vw;
  max-width: 90vw;
  width: 630px;
  margin: 0 auto;
`;

const BlogPostTitle = styled.h1`
  border-width: 0;
  font-size: 1.6em;
  margin-bottom: 1.1em;
  margin-top: 1.1em;
`;

function Main() {
  const [isLoading, setIsLoading] = React.useState(true);
  const [isNotFound, setIsNotFound] = React.useState(false);
  const [postList, setPostList] = React.useState<
    { path: string; type: string }[]
  >([]);

  useEffect(() => {
    const url = `https://api.github.com/repos/alxtz/obs-vault/git/trees/main?recursive=3&timestamp=${new Date().getTime()}`;

    const fetchData = async () => {
      try {
        const response = await axios.get(url, {
          headers: {
            Authorization: `token ${atob(token)}`,
          },
        });
        const resp = response.data;
        setIsLoading(false);
        setIsNotFound(false);
        setPostList(resp.tree);
        console.log("p", postList);
      } catch (error) {
        console.log("e", error);
        setIsLoading(false);
        setIsNotFound(true);
      }
    };

    fetchData();
  }, []);

  if (isLoading) {
    return <p>Loading...</p>;
  }

  if (isNotFound) {
    return <p>unknown error</p>;
  }

  let notIgnored = postList.filter((file) => file.path[0] !== ".");
  notIgnored = notIgnored.filter((file) => file.type !== "tree");
  notIgnored = notIgnored.filter((file) => file.path.endsWith(".md"));

  console.log("n", notIgnored);

  if (notIgnored.length === 0) {
    return <div></div>;
  }

  return (
    <>
      <PageContainer>
        <BlogContainer>
          {notIgnored.map((file) => {
            return (
              <div key={file.path}>
                <Link to={`/p/${file.path}`}>
                  {TITLE_MAP[file.path] ? TITLE_MAP[file.path] : file.path}
                </Link>
              </div>
            );
          })}
        </BlogContainer>
      </PageContainer>
    </>
  );
}

const CodeBlock = {
  code({ node, inline, className, children, ...props }: any) {
    const match = /language-(\w+)/.exec(className || "");
    return !inline && match ? (
      <SyntaxHighlighter
        style={oneLight}
        language={match[1]}
        PreTag="div"
        {...props}
      >
        {String(children).replace(/\n$/, "")}
      </SyntaxHighlighter>
    ) : (
      <code className={className} {...props}>
        {children}
      </code>
    );
  },
};

export default App;
